@import url(https://use.typekit.net/esl4nln.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "montserrat", sans-serif;
}

body {
  touch-action: manipulation;
  background-color: #fafafa;
}

.App {
  text-align: center;
  margin: 0 auto;
}

.gifContainer {
  display: flex;
  margin: 100px 0 0;
  width: 100%;
  height: 200px;
  align-content: center;
  justify-content: center;
}

.gifContainer img {
  max-width: 200px;
}

.gifs {
  width: 912px;
  margin: 0 auto;
  max-height: 100vh;
}

.gif {
  margin: 12.5px;
  width: 279px;
  height: 279px;
  float: left;
  position: relative;
}

.active {
  border: 4px solid white;
  box-sizing: border-box;
}

.gif .inactive {
  border-radius: 10px;
  width: 279px;
  height: 279px;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .4);
  z-index: 1;
}

.gif > img  {
  box-shadow: 5px 5px 20px 0px rgba(0,0,0,0.45);
  width: 100%;
  height: 100%;
  border-radius: 10px;
}

.gif:nth-child(1) img, .gif:nth-child(4) img {
  -webkit-filter: brightness(200%);
          filter: brightness(200%);
}

.gif .votes {
  position: absolute;
  bottom: 5px;
  font-size: 2em;
  color: white;
  display: flex;
  align-items: center;
  justify-items: center;
  padding: 10px;
  z-index: 2;
}

.gif .current {
  position: absolute;
  bottom: 100px;
  text-align: center;
  font-size: 2em;
  color: white;
  text-shadow: 1px 1px 10px black;
  width: 279px;
}

.gif .votes img {
  float: left;
  width: 44px;
}

.touch {
  background-color: red;
}

.slider .MuiSlider-thumb:not(.MuiSlider-active) {
  -webkit-transition: left 1s cubic-bezier(0.22, 0.61, 0.36, 1);
  transition: left 1s cubic-bezier(0.22, 0.61, 0.36, 1);
}

.slider.active .MuiSlider-track {
  -webkit-transition: none;
  transition: none;
}

.slider .MuiSlider-track {
  -webkit-transition: width 1s cubic-bezier(0.22, 0.61, 0.36, 1);
  transition: width 1s cubic-bezier(0.22, 0.61, 0.36, 1);
}

h1 {
  margin-top: 1em;
}

h3 {
  font-weight: 300;
  opacity: 0.6;
  margin-bottom: 1em;
}

.controls {
  width: 100%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-items: center;
}

.controls__control {
  width: 50%;
  padding: 50px;
  box-sizing: border-box;
  float: left;
  cursor: pointer;
  -webkit-transition: .3s cubic-bezier(0, 1.29, 0.01, 0.01);
  transition: .3s cubic-bezier(0, 1.29, 0.01, 0.01);
  -webkit-transition-timing-function: ease-out;
          transition-timing-function: ease-out;
}

.controls__control img {
  max-width: 32px;
}

.controls.wasClicked .controls__minus,
.controls.wasClicked .controls__plus {
  box-shadow: inset -4px -4px 20px 0px rgba(255, 255, 255, 0.5);
}

.controls__minus, .controls__plus, .controls__play {
  background: #F1F3F6;
  border-radius: 20px;
  border: none;
  box-shadow: 30px 30px 80px 0px rgba(55, 84, 170, 0.18), -30px -30px 80px 0px rgba(255, 255, 255, 1), inset 4px 4px 20px 0px rgba(255, 255, 255, 0.5);
}

.controls__minus:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.controls__plus:active {
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
}

.controls__play {
  opacity: .5;
}

.status {
  font-family: 'Helvetica Neue', 'Helvetica', sans-serif;
  font-size: 3em;
  margin-top: -55%;
  font-weight: 300;
}

.status__peopleValue {
  font-weight: bold;
  ;
}

.available {
  opacity: 1;
}
